import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)
import { userInfo } from '@/service/api'
import Store from '@/store'

import Home from '@/views/home/Home.vue'

import Login from '@/views/user-login/index.vue'
import Detail from '@/views/detail/detail.vue'
import Create from '@/views/create/create.vue'
import Edit from '@/views/user-space/edit.vue'

const Recipe = () => import('@/views/recipe/recipe.vue');
const Space = () => import(/* webpackChunkName: "space" */ '@/views/user-space/space.vue');
const MenuList = () => import(/* webpackChunkName: "space" */ '@/views/user-space/menu-list.vue');
const Fans = () => import(/* webpackChunkName: "space" */ '@/views/user-space/fans.vue');

const routes = [
  {
    path: '/',
    name: 'home',
    title: '',
    component: Home
  },
  {
    path: '/login',
    name: 'login',
    component: Login,
    meta: {
      login: true
    }
  },
  {
    path: '/recipe',
    name: 'recipe',
    title: '',
    component: Recipe
  },
  {
    path: '/detail',
    name: 'detail',
    title: '',
    component: Detail
  },
  {
    path: '/space',
    name: 'space',
    title: '',
    component: Space,
    meta: {
      login: true
    },
    redirect: {
      name: 'works'
    },
    children: [
      {
        path: '/works',
        name: 'works',
        title: '',
        component: MenuList,
        meta: {
          login: true
        }
      },
      {
        path: 'fans',
        name: 'fans',
        title: '',
        component: Fans,
        meta: {
          login: true
        }
      },
      {
        path: 'following',
        name: 'following',
        title: '',
        component: Fans,
        meta: {
          login: true
        }
      },
      {
        path: 'collection',
        name: 'collection',
        title: '',
        component: MenuList,
        meta: {
          login: true
        }
      }
    ]
  },
  {
    path: '/create',
    name: 'create',
    title: '',
    component: Create,
    meta: {
      login: true
    }
  },
  {
    path: '/edit',
    name: 'edit',
    title: '',
    component: Edit,
    meta: {
      login: true
    }
  },
  {
    path: '*',
    name: 'noFound',
    title: '',
    redirect: {
      name: 'home'
    }
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach(async (to, from, next) => {
  const token = localStorage.getItem('token');
  const isLogin = !!token;//
  /**
   * 1.token
   * 2.
   */
  const data = await userInfo();
  Store.commit('changeUserInfo',data.data)
  // console.log(data);
  // console.log(to);
  if (to.matched.some(item => item.meta.login)) {//
    if (isLogin) {//
      if (data.error === 400) {//
        next({ name: 'login' });
        localStorage.removeItem('token');
        return;
      }
      if (to.name === 'login') {//loginhome
        next({name:'home'})
      } else {//loginnext
        next();
      }
      return;
    }
    if (!isLogin && to.name === 'login') {//loginnext
      next();
    }
    if (!isLogin && to.name !== 'login') {//loginlogin
      next({name:'login'})
    }
  } else {//
    next();
  }
})

export default router;
